h1,h2,h3,p,span,body,html,div{padding:0;margin:0}h1{font-size:32px}h2{font-size:18px}a{color:inherit;text-decoration:none;transition:color .3s}.grow-wrapper{flex-grow:1;display:flex;align-items:center}body{margin:0;padding:0;position:relative;height:100vh;font-family:sans-serif}.hidden{display:none}#canvas{display:block;position:absolute;width:100vw;height:100vh}.sliders-container{position:absolute;box-sizing:border-box;width:100vw;padding:15px 0px;display:flex;flex-direction:column;align-items:center;background:transparent;transition:transform 0.5s}.sliders-container:nth-of-type(1){top:0px;left:0px}.sliders-container:nth-of-type(1) .slider-wrapper:first-child{position:relative;left:24px;width:calc(90% - 48px)}@media (min-aspect-ratio: 1 / 1){.sliders-container:nth-of-type(1){transform-origin:left top;transform:rotate(-90deg) translateX(-100%);width:100vh}.sliders-container:nth-of-type(1) .slider-wrapper:first-child{left:-24px}}.sliders-container:nth-of-type(2){bottom:0px;right:0px}@media (min-aspect-ratio: 1 / 1){.sliders-container:nth-of-type(2){transform-origin:right bottom;transform:rotate(-90deg) translateX(100%);width:100vh}}.sliders-container label{display:none}.sliders-container .slider-wrapper{opacity:.4;transition:opacity .7s;width:90%;padding:3px;display:flex;justify-content:center}.sliders-container .slider-wrapper:hover,.sliders-container .slider-wrapper.active{opacity:1}.slider{width:100%;box-sizing:border-box;margin:15px 0px;z-index:2}#logo-wrapper{position:absolute;top:0px;left:0px;height:5%;min-width:64px;min-height:64px;background-color:#3333;border-bottom-right-radius:90%;display:flex;justify-content:center;align-items:center;transition:all .3s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#logo-wrapper:hover{background-color:#3335;cursor:pointer}.logo{position:absolute;top:10px;left:10px}
